
.inventory_row {
    /*margin-left: 10px;*/
}
.inventory_cell {
    margin: 2px;
    border: 2px solid #4f4f4f;
    background-color: #294646;
    border-radius: 4px;
    min-width: 40px;
    min-height: 40px;
    max-width: 40px !important;
    max-height: 40px !important;
}
.inventory_cell:hover {
    background-color: #698394;
    cursor: pointer;
}
.selectedItem {
    background-color: green;
    margin: 2px;
    border: 2px solid #3d3d3d;
    border-radius: 4px;
    min-width: 40px;
    min-height: 40px;
    max-width: 40px !important;
    max-height: 40px !important;
}
.selectedItem:hover {
    background-color: #009e00;
    cursor: pointer;
}



