
.enemy_col {
    cursor: pointer;
    border: 2px solid #4f4f4f;
    background-color: #142929;
    border-radius: 4px;
    /* margin: 2px; */
    max-height: 35px;
}
.enemy_col:hover {
    cursor: pointer;
    border: 2px solid #4f4f4f;
    background-color: #557081;
    border-radius: 4px;
    /* margin: 2px; */
    max-height: 32px;
}



