
@import url('https://fonts.googleapis.com/css?family=Droid+Sans+Mono|PT+Mono');
* {
    font-family: "Droid Sans Mono", monospace;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
}

#main_container {
    /*padding: 10px;*/
    /*height: 100%;*/
}
#help {
    position: absolute;
    /* left: 50; */
    right:0px;
    top: 0px;
}
#git_logo {
    position: absolute;
    padding: 20px;
    bottom: 0px;
    right: 0px;
}
.mouse_controls {
    position: absolute;
    /* min-width: 200px; */
    bottom: 0px;
    left: 90%;
}
.loading {
    position: absolute;
    padding: 20px;
    /* min-width: 200px; */
    bottom: 50%;
    left: 25%;
}
.test {
    background-color: #824d03;
}
canvas {
    padding: 0;
    margin: 0;
}

/* Overriding Vuetify's tool tip so that it is centered :) */
[data-tooltip] {
    position: relative;
    text-align: center;
}
.modal {
    border: 2px solid #3d3d3d;
    border-radius: 4px;
    background-color: black;
    color: white;
    width: 400px;
    padding: 10px;
    /*height: 600px;*/
    position: absolute;
    left: 20%;
    top: 25%;
    /*margin-left: -150px;*/
    z-index: 2;
    /*margin-top: -150px;*/
}



