
.ability {
    cursor: pointer;
    border: 2px solid #4f4f4f;
    border-radius: 4px;
}
.sprite {
    cursor: pointer
}
.sprite:hover {
    cursor: pointer;
    background-color: #565656;
    border-radius: 2px;
}
.spriteIsSelected {
    cursor: pointer;
    background-color: rgb(103, 141, 255);
    border-radius: 2px;
}



